.slider {
  margin-top: 80px;
  margin-bottom: 85px;

  @media screen and (min-width: 768px) {
    margin-top: 120px;
    margin-bottom: 120px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 125px;
    margin-bottom: 130px;
  }
}
.slider-block {
  position: relative;
}
.swiper-container {
  width: 100%;
  margin: 0 auto;
  //   margin: 0 auto;
  //   margin-right: 20px;
  //   margin-left: 20px;

  @media screen and (min-width: 768px) {
    width: 620px;
  }

  @media screen and (min-width: 1280px) {
    width: 926px;
  }
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.slider-image {
  width: 85px;
  height: 85px;
  margin-bottom: 15px;
}

.slider-description {
  background-color: #fafafa;
  line-height: 2.55;
  padding: 20px 50px;
  font-weight: 500;
  font-size: 12px;
  text-align: center;

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
    line-height: 2.16;
    font-size: 14px;
    text-align: center;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    text-align: center;
    line-height: 1.86;
  }
}

.slider-description__underline {
  display: inline-block;
  position: relative;

  padding: 0 10px;
  background-image: linear-gradient(transparent, transparent 29px, #e1e1e1 0);
  background-size: 100% 30px;
  z-index: 1;

  @media screen and (min-width: 768px) {
    padding: 0 30px;
  }
}

.slider-description__underline::before {
  content: '';
  position: absolute;
  display: block;
  width: 55px;
  height: 40px;

  left: 0;
  top: -10px;
  background: url(../images/apostrof1.svg) no-repeat;
  z-index: -1;
}

.slider-dots {
  margin-top: 15px;
  text-align: center;
}

.slider-dots__dot {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: red;
}
.slider-dots__dot:not(:last-child) {
  margin-right: 10px;
}

.slider-title {
  margin-top: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.86;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}

.swiper-pagination-list {
  text-align: center;
  margin-top: 35px;
}
.swiper-pagination-item {
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: #d9d9d9;
  border-radius: 0;
  border-radius: 50%;
  opacity: 1;
  &:first-child {
    border-radius: 0;
    background-image: url('../images/icon-home-active.svg');
    background-color: transparent;
  }
  &:not(:last-child) {
    margin-right: 18px;
  }
}
